import { setCacheNameDetails, cacheNames } from "workbox-core";
import { registerRoute } from "workbox-routing";
import { CacheFirst } from "workbox-strategies";
import { RangeRequestsPlugin } from "workbox-range-requests";
import { CacheableResponsePlugin } from "workbox-cacheable-response";
import { precacheAndRoute } from "workbox-precaching";

if (drupalSettings && drupalSettings.urls && drupalSettings.cache_version) {
  // Use separate cache for Workbook but prepend Drupal PWA cache version.
  setCacheNameDetails({
    suffix: drupalSettings.cache_version,
  });

  // Register and precache provided URLs but ignore any query params.
  precacheAndRoute(drupalSettings.urls, {
    ignoreURLParametersMatching: [/.*/],
  });

  const audioUrls = drupalSettings.activity_audios.map((item) => item.url);
  // Before precaching audio URLs, apply range request plugin to them.
  // It will allow playing different types of audio from cache.
  // See https://developers.google.com/web/tools/workbox/guides/advanced-recipes#cached-av
  registerRoute(
    ({ url }) => audioUrls.includes(url),
    new CacheFirst({
      // Use the same cache bin as precacheAndRoute().
      cacheName: cacheNames.precache,
      matchOptions: {
        ignoreVary: true,
        ignoreSearch: true,
      },
      plugins: [
        new CacheableResponsePlugin({ statuses: [200] }),
        new RangeRequestsPlugin(),
      ],
    })
  );

  if (drupalSettings.activity_audios) {
    precacheAndRoute(drupalSettings.activity_audios, {
      ignoreURLParametersMatching: [/.*/],
    });
  }
}
